import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPindahBarang = (
  row1isi = "",
  row2isi = "",
  username = "",
  tanggal = "",
  validby = "",
  data
) => {
  // initialize jsPDF
  const doc = new jsPDF();
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = [];
  let finalY = 40;
  doc.setFontSize(18);
  doc.text("LAPORAN PINDAH BARANG", 14, 15);
  doc.setFontSize(10);
  console.log(data);
  data.forEach((list, index) => {
    tableColumn = [
      [
        {
          content: `NO BON : ${list.no_bon}`,
          colSpan: 6,
        },
      ],
      [
        {
          content: `TANGGAL : ${list.tanggal}`,
          colSpan: 6,
        },
      ],
      [
        {
          content: `LOKASI ASAL : ${list.lokasi_asal}`,
          colSpan: 3,
        },
        {
          content: `LOKASI TUJUAN : ${list.lokasi_tujuan}`,
          colSpan: 3,
        },
      ],
      [
        {
          content: `KETERANGAN : ${list.keterangan}`,
          colSpan: 6,
        },
      ],
      [
        {
          content: `NO`,
        },
        {
          content: `KODE JENIS`,
        },
        {
          content: `NAMA JENIS`,
        },
        {
          content: `BRUTO`,
        },
        {
          content: `KADAR JUAL`,
        },
        {
          content: `NETTO`,
        },
        {
          content: `ONGKOS KIRIM`,
        },
      ],
    ];
    list.detail_barang.forEach((barang, index) => {
      let rows = [
        ++index,
        barang.kode_jenis,
        barang.nama_jenis,
        barang.bruto,
        barang.kadar_jual,
        barang.netto,
        barang.ongkos_rp,
      ];
      tableRows.push(rows);
      console.log(tableRows);
    });
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      foot: footRows,
      startY: index === 0 ? 40 : finalY + 4,
      theme: "plain",
      rowPageBreak: "avoid",
      pageBreak: "avoid",
      margin: { top: 20 },
      bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
      headStyles: {
        lineWidth: 0.02,
        lineColor: "#000",
        fillColor: [212, 212, 211],
      },
    });
    finalY = doc.autoTableEndPosY();
    tableRows = [];
    footRows = [];
  });

  doc.text(`User	: ${username}`, 14, finalY + 10);
  doc.text(`Cetak	: ${tanggal}`, 14, finalY + 16);
  doc.text(`Valid	: ${validby}`, 14, finalY + 22);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN PINDAH BARANG",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN PINDAH BARANG</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPindahBarang;
